import React from 'react';
import '../components/card.css'; 



const cards = require('../../gatsby-config').siteMetadata.cards;

const ThankYouCardTemplate = ({ pageContext }) => {
  const { slug } = pageContext;
  const cardData = cards[slug];


    return (
      <div className="card-container">
        <div className="card">
          <div className="message">
            <p className="message">Dear {cardData.name},</p>
            <p className="message">Thank you very much for {cardData.item}.</p>
            <p className="signoff">With love from Sinead and Fraser</p>

          </div>
        </div>
      </div>
    );
  }



export default ThankYouCardTemplate;
